.header-nav {
  position: sticky;
  box-shadow: 0px 10px 20px #ccc;
  margin-bottom: 10px;
}

.header-ul {
  display: flex;
  flex: 1;
  justify-content: space-between;
}

.header-h2 {
  font-family: monospace;
  font-size: 30px;
}
.logout-btn {
  padding: 10px;
  background: rgb(13, 82, 139);
  border: none;
  border-radius: 2px;

}

.logout-btn:hover {
  background: red;
}